body {
  margin: 10;
  padding: 10;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  background-color: #eff3f7;
  align-items: center;
  height: 100%;
  width: 100%;
}

html,
body,
#poatao,
.container {
  height: 100%;
  display: flex;
  justify-content: center;
}
.loader {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.movies {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: flex-start;
  padding: 50px;
  width: 80%;
  padding-top: 70px;
}
.movies .movie {
  width: 45%;
  background-color: white;
  margin-bottom: 70px;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  min-width: 500px;
  font-weight: 300;
  padding: 20px;
  border-radius: 5px;
  position: relatvie;
  color: #b2b3bc;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}
.movies img {
  position: relative;
  top: -70px;
  max-width: 120px;
  margin-right: 30px;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.movie .movie__title,
.movie .movie__year {
  margin: 0;
  font-weight: 300;
}
.movie .movie__title {
  margin-bottom: 5px;
  font-size: 24px;
  color: hsl(0, 0%, 17%);
  text-decoration: none;
}

.movie h3 {
  margin-bottom: 5px;
  font-size: 24px;
  color: #b2b3bc;
}
.movie .movie__year,
.movie .movie__genres {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  margin: 5px 0px;
}
.movie__genres li {
  margin-right: 10px;
  font-size: 14px;
}
